.btn {
    background-color: #55c3d0;
    border: 1px solid #4f6469;
    border-radius: 5px;
    font-size: 18px;
    font-family: monospace;
    color: white;
    padding: 5px;
    margin-left: 10px;
    text-align: center;
    cursor: pointer;
}

.btn:hover {
    background-color: #4f6469;
}